@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-primary: rgb(22, 0, 121);
}

* {
  margin: 0;
  padding: 0;
}

.App {
  position: relative;
  overflow-x: hidden;
}

.App::after {
  content: "";
  display: block;
  position: absolute;
  top: -200px;
  right: 100px;
  background-color: rgba(255, 68, 0, 0.226);
  border-radius: 50%;
  width: 500px;
  height: 600px;
  z-index: -1;
  transform: rotate(-20deg);
  /* transform: scale(0.5); */
  transform-origin: 100% 0;
}

.banner{margin:50px 0}.banner__container{opacity:0;padding:100px 50px;width:80%;max-width:1680px;margin:0 auto;background-image:url(/furniture/static/media/banner.c8106259.jpg);background-repeat:no-repeat;background-size:120%;background-position:center;border-radius:50px}@media screen and (max-width: 400px){.banner__container{width:100%;padding:0;box-shadow:0px 5px 5px -5px gray;border-radius:0}}.banner__container>div{display:inline-block;padding:20px;background-color:rgba(255,255,255,0.5);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);border-radius:5px;max-width:500px}.banner__container>div h1{font-size:3em;color:black}@media screen and (max-width: 400px){.banner__container>div h1{font-size:2em}}.banner__container>div h1 span{color:orangered}.banner__container>div p{margin:20px 0;font-weight:500;font-size:1.2em}.banner__input{padding:10px 10px;background-color:whitesmoke;border-radius:30px;display:flex}.banner__input input{flex:1 1;padding:0 10px;border:none;background-color:transparent}.banner__input button{padding:10px 20px;background-color:orangered;color:white;border:none;border-radius:20px}

section{padding:50px 0}.feature{width:80%;max-width:1680px;margin:50px auto;display:flex}@media screen and (max-width: 400px){.feature{width:90%;flex-direction:column-reverse;margin:auto}}.feature__list{flex:1 1;margin-right:50px}@media screen and (max-width: 400px){.feature__list{margin-right:0}}.feature__list h1{opacity:0;color:var(--color-primary);font-size:3em;margin-bottom:20px;max-width:200px}@media screen and (max-width: 400px){.feature__list h1{font-size:2em;margin:20px 0;max-width:unset}}.feature__list ul{border-left:5px solid rgba(255,68,0,0.685);list-style:none;padding-left:20px}@media screen and (max-width: 400px){.feature__list ul{display:flex;border:none;padding:0;width:100%}}.feature__list ul li{padding:10px 0;transform:translateX(-100%);opacity:0}@media screen and (max-width: 400px){.feature__list ul li{padding:10px}}.feature__list ul li a{text-decoration:none;color:gray;font-weight:500;font-size:2em;display:block}@media screen and (max-width: 400px){.feature__list ul li a{font-size:1em}}.feature__list ul li a:hover{transition:all 0.3s ease;color:orangered;transform:translateX(10px)}.feature__show{display:flex}.feature__show .card{opacity:0;flex:1 1}.feature__show .card h2{font-size:2em;color:var(--color-primary);margin:20px 0 20px 0}.feature__show .card h2 span{font-size:0.8em}.feature__show .card p{color:orangered}.feature__show .card h4{color:gray;text-align:justify;margin:20px 0;font-size:1.5em;font-weight:400;max-width:400px}@media screen and (max-width: 400px){.feature__show .card h4{font-size:1em}}.feature__show .card__img{display:flex;justify-content:space-around}.feature__show .card__img img{flex:1 1;max-width:150px;background-color:whitesmoke;margin-right:20px;padding:10px}@media screen and (max-width: 400px){.feature__show .card__img img{max-width:100px;margin:0}}.feature__show .card__btn{display:flex;align-items:center;justify-content:space-between}.feature__show .card__btn button{background-color:orangered;color:white;padding:10px 20px;border-radius:20px;border:none;text-transform:uppercase}.feature__show .card__btn a{text-decoration:none;color:orangered}.feature__show>img{opacity:0;max-width:400px}@media screen and (max-width: 400px){.feature__show>img{display:none}}

.footer{display:flex;padding:20px 0;margin:0 auto;width:80%;max-width:1680px}@media screen and (max-width: 400px){.footer{width:90%}}.footer__left{display:flex;flex-direction:column;justify-content:flex-end;color:gray}.footer__left>p{margin-top:50px}@media screen and (max-width: 400px){.footer__left>p{margin-top:20px}}.footer__left>.icons>:not(:last-child){margin-right:20px}.footer__right{flex:1 1;display:flex;justify-content:flex-end}.footer__right ul{color:gray;font-weight:500;list-style:none}.footer__right ul>li{margin-top:10px}.footer__right ul:not(:last-child){margin-right:50px}@media screen and (max-width: 400px){.footer__right ul:not(:last-child){margin-right:20px}}.footer__right ul a{text-decoration:none;font-weight:400;color:gray}.footer__right ul>a{margin-top:50px;display:block}@media screen and (max-width: 400px){.footer__right ul>a{margin-top:20px}}

.set{position:relative;display:flex;width:80%;max-width:1680px;margin:50px auto;padding:50px 0}@media screen and (max-width: 400px){.set{padding:0}}.set::before{content:"";background-image:url(/furniture/static/media/set.c0f5ca99.png);display:block;position:absolute;background-size:contain;background-repeat:no-repeat;top:25%;right:0;bottom:0;width:80%;min-width:500px;z-index:-1;transform:scale(0)}@media screen and (max-width: 400px){.set::before{width:90%;top:35%;min-width:unset}}.set::after{content:"";display:block;width:70%;position:absolute;top:0;bottom:0;right:-250px;background-color:rgba(133,77,77,0.527);z-index:-2;border-radius:100px;transform:translateX(100%)}.set__describe{flex:0.5 1;max-width:400px}@media screen and (max-width: 400px){.set__describe{flex:1 1}}.set__describe>.created{margin-bottom:50px;opacity:0}.set__describe>.created p{color:gray;font-size:1em;font-weight:500;letter-spacing:5px}.set__describe>.created h1{color:var(--color-primary);margin:20px 0;font-size:3em}@media screen and (max-width: 400px){.set__describe>.created h1{font-size:2em}}.set__describe>.created .team{display:flex;margin-top:20px}.set__describe>.created .team>:not(:last-child){margin-right:20px}.set__describe>.desc{opacity:0;margin-top:50px;display:inline-block}.set__describe>.desc h2{color:var(--color-primary);font-size:2em}.set__describe>.desc h2 span{font-size:0.7em}.set__describe>.desc p{color:orangered}.set__describe>.desc h4{max-width:300px;margin:20px 0;line-height:30px;color:gray;font-weight:500;font-size:1.2em}.set__describe>.desc .submit{margin:20px 0;display:flex;align-items:center}.set__describe>.desc .submit button{border:none;background-color:orangered;padding:10px 20px;color:white;border-radius:20px;margin-right:20px}.set__describe>.desc a{text-decoration:none;color:orangered}

.header{display:flex;width:80%;max-width:1680px;margin:0 auto;padding:50px 0;background-image:url(/furniture/static/media/chair.38901fc1.png);background-repeat:no-repeat;background-size:600px;background-position:100% 50%}@media screen and (max-width: 400px){.header{width:90%;background-image:none;padding:20px 0}}.header__left{flex:0.5 1;min-width:400px;max-width:550px}@media screen and (max-width: 400px){.header__left{min-width:unset;flex:1 1}}.header__left h1{font-size:4rem;color:var(--color-primary);margin:0 0 20px}.header__left h1 span{color:rgba(255,68,0,0.719)}@media screen and (max-width: 400px){.header__left h1{font-size:3em}}.header__left p{color:#acacac;max-width:200px;font-weight:500}.header__left form{display:flex;background-color:#eee;border-radius:20px;margin:50px 0;padding:10px}@media screen and (max-width: 400px){.header__left form{margin:20px 0}}.header__left form input{flex:1 1;border:none;background-color:transparent;padding:0 10px}.header__left form input:focus{outline:none}.header__left form button{flex:0.3 1;background-color:orangered;padding:10px 20px;border-radius:20px;color:white;border:none}.header__left>div{display:flex}.header__left>div>div{box-shadow:1px 1px 5px lightgray;flex:1 1;text-align:center;padding:10px}.header__left>div>div h2{color:#7e5b2e;font-size:2em}.header__left>div>div p{font-weight:400}.header__left>div>div:not(:last-child){margin-right:10px}

.navbar{display:flex;justify-content:space-between;align-items:center;color:var(--color-primary);padding:20px 0;width:80%;max-width:1680px;margin:0 auto}@media screen and (max-width: 400px){.navbar{width:90%}}.navbar__logo{font-family:"Great Vibes", cursive;font-size:3em}.navbar__nav{list-style:none;display:flex}@media screen and (max-width: 400px){.navbar__nav{display:none}}.navbar__nav li a{text-decoration:none;font-weight:500;color:inherit;display:block;padding:10px 20px;box-sizing:border-box}.navbar__nav li a:hover{background-color:orangered;box-shadow:0 2px var(--color-primary);color:white;transition:all 0.1s ease-in}.navbar__option{display:flex}.navbar__option>span{background-color:orangered;padding:10px;display:flex;border-radius:10px}.navbar__option>span:not(:last-child){margin-right:20px}.navbar__option>span>*{color:white;margin:auto}.navbar__option>span:hover{background-color:rgba(255,68,0,0.87);cursor:pointer;box-shadow:1px 1px 5px gray;transition:background-color 0.2s ease}

.show{opacity:1 !important;transition:all 1s ease}.view{width:80%;max-width:1680px;margin:50px auto;display:flex;justify-content:center;align-items:center;padding:50px 0;flex-wrap:wrap;opacity:0}@media screen and (max-width: 400px){.view{width:90%;margin:0 auto;flex-direction:column}}.view__product{flex:1 1;position:relative}.view__product>img{max-width:250px}.view__product>button{width:30px;height:30px;border:none;position:absolute;box-shadow:1px 1px 3px gray}.view__product>button:hover{cursor:pointer;transform:scale(1.2);transition:transform 0.2s ease}.view__product>button:nth-child(2){top:20%;background-color:brown}.view__product>button:nth-child(3){top:35%;background-color:yellow}.view__product>button:last-child{top:50%;background-color:blue}.view__product::after{content:"";display:block;background-color:lightgray;width:300px;height:200px;border-radius:50%;position:absolute;top:0;left:0;z-index:-1}@media screen and (max-width: 400px){.view__product::after{width:100%}}.view__details{flex:1.2 1;display:grid;grid-template-columns:0.8fr 1fr}@media screen and (max-width: 400px){.view__details{grid-template-columns:1fr;flex:1 1}}@media screen and (max-width: 400px){.view__details>.info{grid-column-start:1;grid-column-end:3}}.view__details>.info h4{color:gray}.view__details>.info h1{color:var(--color-primary)}.view__details>.info h1 span{font-size:0.8em}.view__details>.info h1:last-child{margin:20px 0}.view__details>.info p{color:rgba(255,68,0,0.788)}.view__details>.detail{display:flex;flex-direction:column}@media screen and (max-width: 400px){.view__details>.detail{display:none}}.view__details>.detail p{color:gray;text-align:justify;min-width:200px}.view__details>.detail a{color:orangered;text-decoration:none;margin-left:auto;margin-top:20px}.view .submit{grid-column-start:1;grid-column-end:3;display:flex}.view .submit button{background-color:orangered;border:none;padding:10px 20px;border-radius:20px;color:white}.view .submit button:not(:last-child){margin-right:20px}

